import _base from './_base-module';

const state = {
    startAt: 100,
    mappingType: 'fatalities',
    logScale: true,
    perCapita: false,
    growthRatePer: 4
};

const getters = {
    ..._base.getters
};

const actions = {};

const mutations = {
    updateProperty(state, payload) {
        state[payload.key] = payload.value;
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}