<script>
    import country from "./country";

    export default {
        name: 'selected-countries',
        components: {
            country
        },
        props: {},
        computed: {
            countries() {
                return this.$store.state.countries.all.filter(c => c.active);
            }
        },
        methods: {}
    }
</script>


<template>
    <div class="selected-countries">
        <country
            v-for="country in countries"
            :country="country"/>
    </div>
</template>


<style lang="scss">
    @import '@/styles/variables.scss';

    .selected-countries {
        display: flex;
        flex-wrap: wrap;
    }
</style>