<script>
    export default {
        name: 'map-tools',
        components: {},
        props: {},
        computed: {
            startAt: {
                set(value) {
                    this.$store.commit('settings/updateProperty', {key: 'startAt', value})
                },
                get() {
                    return this.$store.state.settings.startAt;
                }
            },
            growthRatePer: {
                set(value) {
                    this.$store.commit('settings/updateProperty', {key: 'growthRatePer', value})
                },
                get() {
                    return this.$store.state.settings.growthRatePer;
                }
            },
            mappingType: {
                set(value) {
                    this.$store.commit('settings/updateProperty', {key: 'mappingType', value})
                },
                get() {
                    return this.$store.state.settings.mappingType;
                }
            },
            mappingTypeOptions() {
                return ['fatalities', 'cases'];
            }
        },
        methods: {}
    }
</script>


<template>
    <div class="map-tools tool">
        <div>
            Start at <input v-model="startAt"/> (absolute)
            <select
                v-model="mappingType">
                <option
                        v-for="option in mappingTypeOptions">
                    {{option}}
                </option></select>
        </div>
        <div>
            Growth average per  <input v-model="growthRatePer"/> days.
        </div>
    </div>
</template>


<style lang="scss">
    @import '@/styles/variables.scss';

    .map-tools {

        input {
            width: 40px;
            padding: 4px;
            margin: 0 4px;
        }
    }
</style>